html, body {
    height: 100%;
}

body {
    margin: 0;
}

.main-container {
    margin: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    margin-top: 5em;
}

.inner-container {
    display: inline-block;
}

.switchTriviaButton {
    background: #66c2ff;
  background-image: -webkit-linear-gradient(top, #66c2ff, #1d75ab);
  background-image: -moz-linear-gradient(top, #66c2ff, #1d75ab);
  background-image: -ms-linear-gradient(top, #66c2ff, #1d75ab);
  background-image: -o-linear-gradient(top, #66c2ff, #1d75ab);
  background-image: linear-gradient(to bottom, #66c2ff, #1d75ab);
  -webkit-border-radius: 10;
  -moz-border-radius: 10;
  border-radius: 10px;
  -webkit-box-shadow: 0px 1px 3px #666666;
  -moz-box-shadow: 0px 1px 3px #666666;
  box-shadow: 0px 1px 3px #666666;
  color: #ffffff;
  font-size: 19px;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
  outline: none;
  border: none;
  margin: 10px;
}

.switchTriviaButton:hover {
    background: #3cb0fd;
  background-image: -webkit-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -moz-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -ms-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -o-linear-gradient(top, #3cb0fd, #3498db);
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
  text-decoration: none;
}
